import {
  getAbsoluteUrl,
  _extends,
  isEmpty
} from './util'
import {
  log,
} from './log'

// 区分设备系统
var ua = navigator.userAgent;
var android = ua.match(/(Android);?[\s\/]+([\d.]+)?/);
var ipad = ua.match(/(iPad).*OS\s([\d_]+)/);
var ipod = ua.match(/(iPod)(.*OS\s([\d_]+))?/);
var iphone = !ipad && ua.match(/(iPhone\sOS)\s([\d_]+)/);
var isFromApp = !!ua.toLowerCase().match(/DFYJ/i);

// 初始化桥接对象
var init = function (bridge) {
  bridge.init(function (message, responseCallback) {
    if (responseCallback)
      responseCallback(data)
  })

  return bridge
}

/**
 * 初始化桥接对象
 * connectWebViewJavascriptBridge
 * @param callback
 */
var ready = function (callback) {
  let __cb___ = function(__bridge___) {
    log('ready end')
    callback(__bridge___)
  }

  if (isFromApp) {
    if (ipad || iphone || ipod) {
      // 新版本兼容IOS
      if (window.WebViewJavascriptBridge) {
        log('ready sync start')
        return __cb___(WebViewJavascriptBridge);
      }
      log('ready async start(push)')
      if (window.WVJBCallbacks) {
        return window.WVJBCallbacks.push(__cb___);
      }
      log('ready async start(iframe)')
      window.WVJBCallbacks = [__cb___];
      var WVJBIframe = document.createElement('iframe');
      WVJBIframe.style.display = 'none';
      WVJBIframe.src = 'https://__bridge_loaded__';
      document.documentElement.appendChild(WVJBIframe);
      setTimeout(function () {
        document.documentElement.removeChild(WVJBIframe)
      }, 0)
    } else if (android) {
      // 旧版本兼容Android
      if (window.WebViewJavascriptBridge) {
        log('ready sync start')
        __cb___(init(WebViewJavascriptBridge))
      } else {
        log('ready async start')
        document.addEventListener('WebViewJavascriptBridgeReady', function () {
          // 以异步形式加载
          __cb___(init(WebViewJavascriptBridge))
        }, false)
      }
    } else {
      log('other platform ready sync start')
      __cb___()
    }
  } else {
    log('non app ready sync start')
    __cb___()
  }
}

// 空函数
var noop = function () {}

/**
 * 成功消息体的包装
 * @param payload 成功对象
 */
// const wrapOkPayload = (payload = {}) => ({
//     error: false,
//     info: 'ok',
//     payload: payload,
// })
const wrapOkPayload = (payload = {}) => (payload)

/**
 * 失败消息体的包装
 * @param info 错误描述
 */
const wrapNotOkPayload = (code = -1, info = 'error') => ({
  // error: true,
  code: code,
  info: info,
})

/**
 * APP WebView后退，后退到栈的最外层
 * @param options
 */
var onBack = function (options) {
  // 默认配置
  var _options = {
    complete: noop
  };
  // 复制
  _options = _extends({}, _options, options);

  log('onBack->' + JSON.stringify(_options))
  WebViewJavascriptBridge.callHandler('back', _options, function (response) {
    log('onBack<-' + JSON.stringify(response))
    _options.complete(response);
  });
}

/**
 * APP WebView刷新
 * @param options
 */
var onRefresh = function (options) {
  var _options = {
    complete: noop
  };
  _options = _extends({}, _options, options);

  log('onRefresh->' + JSON.stringify(_options))
  WebViewJavascriptBridge.callHandler('refresh', _options, function (response) {
    log('onRefresh<-' + JSON.stringify(response))
    _options.complete(response);
  });
}

/**
 * 设置原生导航条
 * @param options
 * {
 *    "title": "xxxxx",
 *    "type": "0"       // 0-默认 1-切换类
 * }
 */
var onNavigationBar = function (options) {
  var _options = {
    complete: noop
  };
  _options = _extends({}, _options, options);

  log('onNavigationBar->' + JSON.stringify(_options))
  WebViewJavascriptBridge.callHandler('navigationBar', _options, function (response) {
    log('onNavigationBar<-' + JSON.stringify(response))
    _options.complete(response);
  });
}

/**
 * 打开一个新的webview
 * @param options
 * {
 *    "title": "xxxxx",      // 导航名称，如果html设置title，以html为主
 *    "uri": "xxxxxxx",      // webview请求的路由地址，目前只支持http协议，今后可支持shema协议
 *    "locktype": 0,         // 0-不需要手势密码；1-手势密码锁屏；2-资金账号锁屏；
 * }
 */
var onPushView = function (options) {
  var _options = {
    title: ''
  };
  _options = _extends({}, _options, options);

  if (!!_options.uri) {
    if (_options.uri.indexOf('http') != -1 || _options.uri.indexOf('https') != -1) {

    } else {
      _options.uri = getAbsoluteUrl(_options.uri);
    }
  }

  log('onPushView->' + JSON.stringify(_options))
  WebViewJavascriptBridge.callHandler('pushView', _options, function (response) {
    log('onPushView<-' + JSON.stringify(response))
  });
}

/**
 * 原生页面将要出现时触发的事件
 * @param callback
 */
var pageWillAppear = function (callback) {
  if (typeof callback == 'function')
    WebViewJavascriptBridge.registerHandler("pageWillAppear", callback)
}

/**
 * H5发送通知给客户端
 * @param options
 */
var onNotify = function (options) {
  var _options = {
    type: 'javascriptNotify'
  };
  _options = _extends({}, _options, options);

  log('onNotify->' + JSON.stringify(_options))
  WebViewJavascriptBridge.callHandler('notify', _options, function (response) {
    log('onNotify<-' + JSON.stringify(response))
  });
}

/**
 * H5注册通知，由客户端进行通知
 * @param type  事件类型名称
 * @param callback  回调处理
 */
var notifyQueue = {};
var subscribeNotify = function (type, callback) {
  if (typeof callback == 'function') {
    //回调函数存在队列中
    notifyQueue[type] = callback;
    var _type = type;
    if (_type == 'sae_video') {
      _type = 'notify';
    }

    log('subscribeNotify->' + type)
    WebViewJavascriptBridge.registerHandler(_type, data => {
      log('subscribeNotify<-' + JSON.stringify(data))
      // //获取data.type才是H5定义的type
      // if (Object.prototype.toString.call(data).toLocaleLowerCase() == '[object String]'.toLocaleLowerCase()) {
      //   data = JSON.parse(data);
      // }
      if (notifyQueue[_type]) {
        notifyQueue[_type](data);
      }
    })
  }
}

/**
 * 调用Schema Url
 * @param options
 */
var onDynamicLink = function (options) {
  var _options = {
    url: ''
  };
  _options = _extends({}, _options, options);

  log('onDynamicLink->' + JSON.stringify(_options))
  WebViewJavascriptBridge.callHandler('dynamicLink', _options, function (response) {
    log('onDynamicLink<-' + JSON.stringify(response))
  });
}

// 存储Request指令订阅Handlers
var requestHandlerMap = {}
// 是否注册了Request指令订阅Handlers
var requestHandlerRegisterFlag = false

/**
 * 调用Request指令（通常作用在一些原生和客户端之间比较特殊的操作）
 * @param operationType     操作指令（见文档）
 * @param payloadOptions    消息体结构
 */
var onRequest = function (operationType, payloadOptions, okCallBack, notOkCallBack) {
  var _options = {
    type: !operationType ? '1' : operationType,
    payload: !payloadOptions ? {} : payloadOptions,
  }

  requestHandlerMap[operationType + '-ok'] = (typeof okCallBack === 'function') ? okCallBack : noop
  requestHandlerMap[operationType + '-notok'] = (typeof notOkCallBack === 'function') ? notOkCallBack : noop

  // IOS同步回调处理
  log('onRequest->' + JSON.stringify(_options))
  WebViewJavascriptBridge.callHandler('request', _options, function (response) {

    if (typeof response == 'string') {
      response = JSON.parse(response);
    }

    log('onRequest<-' + JSON.stringify(response))
    if (response.error || response.error === 'true') {
      if (typeof notOkCallBack === 'function') {
        requestHandlerMap[operationType + '-notok'](wrapNotOkPayload(-1, response.info))
      }
    } else {
      if (typeof okCallBack === 'function') {
        // 防止安卓指令调用成功同步进入的控制
        if ((ipad || iphone || ipod) || response.payload) {
          requestHandlerMap[operationType + '-ok'](wrapOkPayload(response.payload))
        }
      }
    }

  })

  if (!requestHandlerRegisterFlag) {
    // Android异步回调处理
    WebViewJavascriptBridge.registerHandler('request', function (response) {

      if (typeof response == 'string') {
        response = JSON.parse(response);
      }

      if (response.error || response.error === 'true') {
        if (typeof requestHandlerMap[operationType + '-notok'] === 'function') {
          requestHandlerMap[operationType + '-notok'](wrapNotOkPayload(-1, response.info))
        }
      } else {
        if (typeof requestHandlerMap[operationType + '-ok'] === 'function') {
          requestHandlerMap[operationType + '-ok'](wrapOkPayload(response.payload))
        }
      }

    })
    requestHandlerRegisterFlag = true
  }

}

/**
 * 提供给第三方调用中台接口的函数
 * @param functionNo        中台功能号
 * @param payloadOptions    中台消息体
 * @param okCallBack        成功回调函数
 * @param notOkCallBack     失败回调函数
 */
var onApiRequest = function (functionNo, payloadOptions, okCallBack, notOkCallBack) {

  if (isEmpty(functionNo)) {
    throw new Error('functionNo is required ...')
  } else {
    var _options = {
      funcId: functionNo,
      payload: !payloadOptions ? {} : payloadOptions,
    }
  }

  log('onApiRequest->' + JSON.stringify(_options))
  WebViewJavascriptBridge.callHandler('apiRequest', _options, function (response) {
    log('onApiRequest<-' + JSON.stringify(response))

    if (typeof response == 'string') {
      response = JSON.parse(response)
    }

    // 2017-12-26:对脚本逻辑做兼容，IOS认为业务异常和系统异常response.error都是true，ANDROID认为业务异常和业务成功时response.error是false,而系统异常response.error是true

    // IOS认为业务异常和系统异常response.error都是true,业务成功response.error是false
    if (ipad || iphone || ipod) {

      if (response.error || response.error === 'true') {
        // 接口业务异常
        if (response.payload) {
          if (typeof notOkCallBack === 'function') {
            notOkCallBack(wrapNotOkPayload(response.payload.code, response.payload.info))
          }
        }
        // 客户端自身应用级别错误（ios不给payload字段）
        else {
          if (typeof notOkCallBack === 'function') {
            notOkCallBack(wrapNotOkPayload(-1, response.info))
          }
        }
      } else {
        if (typeof okCallBack === 'function') {
          let re = {}
          if (response.payload) {
            if (typeof response.payload.data == 'string') {
              re = JSON.parse(response.payload.data)
            } else {
              re = response.payload.data
            }
          }

          okCallBack(wrapOkPayload(re))
        }
      }

    }
    // ANDROID认为业务异常和业务成功时response.error是false,而系统异常response.error是true
    else {

      if (response.error || response.error === 'true') {
        if (typeof notOkCallBack === 'function') {
          notOkCallBack(wrapNotOkPayload(-1, response.info))
        }
      } else {
        let re = {}
        if (typeof response.payload.data == 'string') {
          re = JSON.parse(response.payload.data)
        } else {
          re = response.payload.data ? response.payload.data : {}
        }

        // 业务异常
        if (response.payload.code != '0') {
          if (typeof notOkCallBack === 'function') {
            notOkCallBack(wrapNotOkPayload(response.payload.code, response.payload.info))
          }
        }
        // 业务成功
        else {
          if (typeof okCallBack === 'function') {
            okCallBack(wrapOkPayload(re))
          }
        }
      }

    }

  })

}

export {
  wrapOkPayload,
  wrapNotOkPayload,

  // ready是一个异步操作,如果需要在页面加载时就调用相关接口,则需把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口,则可直接调用,不需要放在ready函数中
  // 该函数中有一个bridge变量,与下面的bridge使用上一致,只是调用顺序需要额外注意
  ready,
  onBack,
  onRefresh,
  onNavigationBar,
  onPushView,
  onNotify, // H5发送通知给客户端
  subscribeNotify, // 客户端发送通知给H5
  onDynamicLink,
  onRequest,

  // 原生生命周期相关事件
  pageWillAppear,

  // 第三方接口调用
  onApiRequest,
}


